*{
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	user-select: none;
	font-family: var(--tipo_montserrat);
	color: #fff;
	list-style: none;
}

:root{
	font-size: 100%;

	--color_1: rgb(132.32, 250.18, 177.73);
	--color_2: rgba(15.913, 14.942, 16.934, 1.0);
	--color_2_75: rgba(15.913, 14.942, 16.934, .75);
	--color_3: rgb(31.825, 29.883, 33.867);
	--color_4: rgb(250.03, 100.97, 103.45);

	--tipo_shoulders_d: 'Big Shoulders Display', cursive;
	--tipo_shoulders_t: 'Big Shoulders Text', cursive;
	--tipo_montserrat: 'Montserrat', sans-serif;
}

html{
	scroll-behavior: smooth;
}

body{
	background-color: var(--color_3);
}

a{
	text-decoration: none;
}

input{
	border: none;
	outline: none;
}

.titles-container{
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	column-gap: 1rem;
}

h1{
	display: inline-block;
	font-family: var(--tipo_shoulders_d);
	line-height: 1.25em;
	text-transform: uppercase;
	font-size: 2.5rem;
	font-weight: 700;
	letter-spacing: .075em;
}

h2{
	font-family: var(--tipo_shoulders_d);
	font-size: 2rem;
	font-weight: 100;
	text-transform: uppercase;
	letter-spacing: .075em;
	border-bottom: .225rem solid var(--color_1);
	padding-bottom: .25rem;
}

h3{
	font-family: var(--tipo_shoulders_t);
	color: var(--color_1);
	font-size: 1.5rem;
	font-weight: normal;
	letter-spacing: .075em;
}

h4{
	font-weight: 700;
	font-size: 1rem;
	letter-spacing: .075em;
}

p{
	font-family: var(--tipo_montserrat);
	letter-spacing: .075em;
	line-height: 1.25em;
}

header{
	display: flex;
	justify-content: space-between;
	align-items: center;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	padding: 1rem;
	background-color: var(--color_2_75);
	backdrop-filter: blur(10px);
	z-index: 1000;
	box-shadow: 0 0 1rem rgba(0, 0, 0, .5);
	transition: background-color, .3s ease-in-out;
}

.header-activo{
	background-color: var(--color_2);
}

.logotipo-container{
	align-items: center;
	display: flex;
	gap: 1rem;
}

.header-title{
	font-family: var(--tipo_shoulders_d);
	font-size: 2rem;
	font-weight: 100;
	letter-spacing: .075em;
}

.logo-container{
	width: 3rem;
	height: 3rem;
	display: flex;
	justify-content: center;
	align-items: center;
	border-radius: 100%;
	background-color: #fff;
	animation: logo-animado 15s infinite ease-out;
	rotate: 0deg;
	scale: 1.0;
}

.logo-container img{
	width: 65%;
	height: 65%;
}

.menu_estatico{
	display: none;
}

.menu_estatico ul{
	display: flex;
	gap: 1rem;
}

.menu_estatico ul li a{
	padding: 1rem;
	border: .1em solid transparent;
	transition: border, color, .3s ease-in-out;
}

.menu_estatico ul li a:hover{
	border: .1em solid var(--color_1);
	color: var(--color_1);
}

.menu{
	position: relative;
	display: flex;
	align-items: center;
}

.menu svg{
	font-size: 1.5rem;
}

#menuMascara{
	position: absolute;
	width: 100%;
	height: 100%;
	cursor: pointer;
}

.botonera{
	position: fixed;
	margin-top: -100%;
	top: 0;
	left: 0;
	width: 100%;
	padding-top: 6rem;
	padding-bottom: 1rem;
	background-color: rgba(0, 0, 0, .5);
	z-index: 900;
	backdrop-filter: blur(10px);
	transition: margin-top, .3s ease-in-out;
}

.botonera-activa{
	margin-top: 0;
}

.botonera li{
	padding: 0 2rem;
}

.botonera li a{
	width: 100%;
	display: flex;
	justify-content: space-between;
	padding: .5rem 0;
	transition: color, .3s ease-in-out;
}

.botonera li a:hover{
	color: var(--color_4);
}

.botonera li a svg{
	transition: filter, .3s ease-in-out;
}


.botonera li a:hover > svg{
	filter: invert(52%) sepia(90%) saturate(3409%) hue-rotate(324deg) brightness(107%) contrast(101%);
}


main{
	display: flex;
	flex-direction: column;
	z-index: 0;
	width: 100%;
}

.hero-parrafo_container{
	display: flex;
	flex-direction: column;
	gap: 2rem;
	z-index: 200;
}

.parrafo{
	display: flex;
	flex-direction: column;
	gap: 1rem;
}

.seccion{
	width: 100%;
	min-height: 100vh;
	padding: 8rem 1rem 4rem 1rem;
}

#hero{
	display: flex;
	flex-direction: column;
	justify-content: flex-end;
	background-image: url('../images/background-2.png');
	background-position: top center;
	background-repeat: no-repeat;
	background-size: cover;
	background-attachment: fixed;
	filter: brightness(1.1);
}

#hero::before{
	content: '';
	position: absolute;
	left: 0;
	bottom: 0;
	width: 100%;
	height: 85vh;
	background: linear-gradient(to top, rgb(28.825, 26.883, 30.867) 10%, transparent);
	z-index: -100;
}

.light{
	font-weight: 100;
}

.experiencias-container{
	padding: .5rem 0;
	border-top: .1rem solid var(--color_2);
	border-bottom: .1rem solid var(--color_2);
	display: flex;
	gap: 2rem;
	align-items: center;
	justify-content: space-between;
}

.experiencias{
	display: flex;
	align-items: center;
	gap: .5rem;
	opacity: 0;
	scale: 1;
	animation: experienciasAnimacion 5s ease-in-out infinite;
}

.experiencias p{
	font-weight: 700;
	letter-spacing: .1em;
	color: var(--color_4);
}

.logos-container svg{
	font-size: 2.25rem;
	display: none;
	filter: invert(50%) sepia(66%) saturate(1888%) hue-rotate(318deg) brightness(103%) contrast(101%);
}

.logos-container svg:nth-child(1){
	display: inherit;
}

.experiencias-mascara{
	position: absolute;
	width: 100%;
	height: 100%;
	background-color: transparent;
}

#portfolio{
	background-color: var(--color_3);
}

.grid-item{
	width: 100%;
	display: flex;
	flex-direction: column;
	background: linear-gradient(to top, var(--color_2_75), transparent, transparent);
	gap: 1rem;
	padding: 1.5rem;
	box-shadow: .5rem .5rem 1rem rgba(0, 0, 0, .25);
	transition: background, .3s ease-in-out;
}

.grid-item:hover{
	background: linear-gradient(to top, var(--color_2), var(--color_2_75), transparent);
}

.grid-item:hover > .project-img_container img{
	transform: scale(1.05);
}

.project-img_container{
	width: 100%;
}

.project-img_container img{
	width: 100%;
	object-fit: cover;
	transition: transform, .3s ease-in-out;
}

.project-text{
	font-weight: 100;
	font-size: .75rem;
}

.project_button{
	display: flex;
	justify-content: space-between;
	border: .2rem solid #fff;
	color: #fff;
	padding: 1rem;
	transition: border, color, .3s ease-in-out;
	cursor: pointer;
}

.project_button:hover{
	border: .2rem solid var(--color_4);
	color: var(--color_4);
}

.project_button svg{
	transition: filter, .3s ease-in-out;
}

.project_button:hover > svg{
	filter: invert(52%) sepia(90%) saturate(3409%) hue-rotate(324deg) brightness(107%) contrast(101%);
}

.lista_experiencias{
	width: 100%;
	display: flex;
	flex-direction: column;
	gap: 2rem;
}

.experiencia{
	width: 100%;
	display: flex;
	flex-direction: column;
	gap: .5rem;
}

.experiencias-text_container{
	display: flex;
	justify-content: space-between;
}

.experiencias-text_container span:nth-child(1){
	font-style: italic;
}

.barra_container{
	width: 100%;
	height: 2rem;
	border: .1rem solid #fff;
	padding: .25rem;
	cursor: pointer;
}

.barra{
	background-color: var(--color_1);
	height: 100%;
	transition: background-color, .3s ease-in-out;
}

.barra_container:hover > .barra{
	background-color: var(--color_4);
}

#contacto{
	display: flex;
	flex-direction: column;
	gap: 8rem;
	justify-content: space-between;
}

.contact-form_container{
	display: flex;
	justify-content: center;
	width: 100%;
}

.contact-form{
	width: 100%;
	display: flex;
	flex-direction: column;
	gap: 2rem;
	background: linear-gradient(to top, var(--color_2_75), transparent);
	/*background-color: var(--color_2_75);*/
	padding: 2rem;
	max-width: 540px;
	box-shadow: 1rem 1rem 1rem rgba(0, 0, 0, .25);
}

.form-elements_container{
	display: flex;
	flex-direction: column;
	width: 100%;
	gap: .5rem;
}

.form-elements_container label{
	font-style: italic;
	transition: color .3s ease-in-out;
}

.form-elements_container textarea{
	outline: none;
	border: none;
	resize: none;
	height: 12.5rem;
}

.form-input_container{
	display: flex;
	align-items: center;
	border-left: .5rem solid var(--color_2_75);
	padding-left: .5rem;
	width: 100%;
	transition: border-left, .3s ease-in-out;
}

.form-input_container:hover{
	border-left: .5rem solid var(--color_1);
}

.form-input{
	width: 100%;
	background-color: var(--color_2_75);
	padding: 1rem;
}

.warning_button{
	text-align: center;
	border: none;
	outline: none;
	font-size: 1.25rem;
	background-color: #fff;
	color: var(--color_3);
	padding: 1rem;
	transition: background-color, color, .3s ease-in-out;
	cursor: pointer;
}

.warning_button:hover{
	background-color: var(--color_4);
	color: #fff;
}

footer{
	width: 100%;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	z-index: 1000;
	padding: 8rem 1rem 4rem 1rem;
	background-color: rgba(0, 0, 0, .25);
	gap: 4rem;
}

footer p{
	font-size: .75rem;
	font-weight: 100;
}

.redes_container{
	width: 100%;
	display: flex;
	flex-direction: column;
	gap: 2rem;
}

.redes{
	width: 100%;
	flex-direction: column;
	display: flex;
	gap: 1rem;
}

.redes-item_container{
	display: flex;
	align-items: center;
	gap: 1rem;
}

.redes-item_container:hover > span{
	color: var(--color_4);
}

.redes-item_container:hover > svg{
	filter: invert(52%) sepia(90%) saturate(3409%) hue-rotate(324deg) brightness(107%) contrast(101%);color: var(--color_4);
}

.redes-item_container span{
	font-family: var(--tipo_montserrat);
	letter-spacing: .075em;
	font-size: 1rem;
	transition: color, .3s ease-in-out;
}

.redes-item_container svg{
	font-size: 2rem;
	transition: filter, .3s ease-in-out;
}

#portfolio, #experiencia{
	display: flex;
	flex-direction: column;
	gap: 4rem;
}

.texto_celeste{
	color: var(--color_1);
}

.title_fx{
	padding: .25rem .25em;
	background-color: rgba(255, 255, 255, 1);
	color: var(--color_2);
	/*
	background-image: linear-gradient(var(--color_4), var(--color_4));
	background-position: 0% 100%;
	background-repeat: no-repeat;
	background-size: 2% 3rem;
	animation: titleFx 5s infinite ease;
	transition: background-size, color, .2s;
	*/
}

.parrafo_general{
	display: flex;
	flex-direction: column;
	gap: 1.5rem;
}

#warningBackground{
	display: none;
	position: fixed;
	width: 100%;
	height: 100vh;
	backdrop-filter: blur(10px);
	background-color: var(--color_2_75);
	justify-content: center;
	align-items: center;
	padding: 2rem;
	z-index: 1000;
	top: 0;
	left: 0;
}

.warning{
	background-color: var(--color_3);
	display: flex;
	flex-direction: column;
	padding: 2rem;
	gap: 1rem;
	max-width: 320px;
}

@keyframes logo-animado{
	0%{
		rotate: 0deg;
		scale: 1.0;
	}
	95%{
		rotate: 0deg;
		scale: 1.0;
	}
	97.5%{
		rotate: -30deg;
		scale: 1.5;
	}
	100%{
		rotate: 0deg;
		scale: 1.0;
	}
}

@keyframes experienciasAnimacion{
	0%{
		scale: 1;
		opacity: 0;
	}
	10%{
		scale: 1.05;
		opacity: 1;
	}
	15%{
		scale: 1;
	}
	90%{
		opacity: 1;
	}
	100%{
		opacity: 0;
	}
}

@keyframes titleFx{
	0%{
		background-size: 2% 3rem;
	}
	10%{
		background-size: 100% 3rem;
	}
	20%{
		background-size: 2% 3rem;
	}
}

@media only screen and (max-width: 355px){
	:root{
		font-size: 75%;
	}
}

@media only screen and (min-width: 640px){

	footer{
		min-height: 0;
	}

	.redes_container{
		flex-direction: row;
	}

}

@media only screen and (min-width: 840px){

	:root{
		font-size: 80%;
	}

	header{
		padding: 1rem 3rem;
	}

	.seccion{
		padding: 8rem 2rem 4rem 2rem;
	}

	.menu_estatico{
		display: inherit;
	}

	.menu{
		display: none;
	}

	.botonera{
		display: none;
	}

	footer{
		padding: 4rem 2rem 4rem 2rem;
	}

}

@media only screen and (min-width: 1200px){

	header{
		padding: 1rem 8rem;
	}

	.seccion{
		padding: 8rem 8rem 4rem 8rem;
	}

	footer{
		padding: 4rem 8rem 4rem 8rem;
	}

}

